<template>
    <div class="homeCon">
      <div class="searchInput topSelect">
        <el-select v-model="date" :popper-append-to-body = false placeholder="请选择">
          <el-option
            v-for="item in selectOptions"
            :key="item.value"
            :label="item.label"
            :value="item.value">
          </el-option>
        </el-select>
      </div>
      <div class="statisticsCon">
        <div class="statisticsItem">
          <apexchart type=area :options="userChartOptions" :series="userSeries" />
        </div>
        <div class="statisticsItem">
          <apexchart type=area :options="getAwardChartOptions" :series="getAwardSeries" />
        </div>
        <div class="statisticsItem noMargin">

        </div>
      </div>
      <div class="bestTask">
        <div class="taskTitleCon">
          <div class="taskTitle">
            <span class="title">最受欢迎任务</span>
            <span class="tips">TOP10</span>
          </div>
          <div class="taskSearchCon">
            <el-select v-model="date" :popper-append-to-body = false placeholder="请选择">
              <el-option
                v-for="item in selectOptions"
                :key="item.value"
                :label="item.label"
                :value="item.value">
              </el-option>
            </el-select>
          </div>
        </div>
        <div class="homeTableCon">
          <el-table :data="taskTableData" style="width: 100%">
            <el-table-column prop="id" label="序号" width="180"></el-table-column>
            <el-table-column prop="name" label="任务名称" width="180"></el-table-column>
            <el-table-column prop="clickCount" label="点击次数"></el-table-column>
            <el-table-column prop="completeCount" label="完成次数"></el-table-column>
          </el-table>
        </div>
        <div class="getAllData">
          <span class="el-icon-refresh-right"></span>
          全部加载
        </div>
      </div>
      <div class="bestTask bestGoods">
        <div class="taskTitleCon">
          <div class="taskTitle">
            <span class="title">最受欢迎商品</span>
            <span class="tips">TOP10</span>
          </div>
          <div class="taskSearchCon">
            <el-select v-model="date" :popper-append-to-body = false placeholder="请选择">
              <el-option
                v-for="item in selectOptions"
                :key="item.value"
                :label="item.label"
                :value="item.value">
              </el-option>
            </el-select>
          </div>
        </div>
        <div class="homeTableCon">
          <el-table :data="taskTableData" style="width: 100%">
            <el-table-column prop="id" label="序号" width="180"></el-table-column>
            <el-table-column prop="name" label="商品名称" width="180"></el-table-column>
            <el-table-column prop="clickCount" label="点击次数"></el-table-column>
            <el-table-column prop="completeCount" label="购买次数"></el-table-column>
          </el-table>
        </div>
      </div>





    </div>
</template>

<script>
    export default {
      name: "Home",
      data() {
        return {
          date: '',
          selectOptions: [
            {
              value: '1',
              label: '今日'
            }, {
              value: '2',
              label: '最近3日'
            }, {
              value: '3',
              label: '最近7日'
            }, {
              value: '4',
              label: '最近30日'
            }, {
              value: '5',
              label: '全部'
            }
          ],
          taskTableData: [],
          chartOptions: {
            chart: {
              height: 230,
              zoom: {
                enabled: false
              },
              toolbar: {
                show: false
              }
            },
            dataLabels: {
              enabled: false
            },
            stroke: {
              curve: 'smooth'
            },
            title: {
              text: '',
              align: 'left',
              margin: 0,
              offsetX: 10,
              offsetY: 10,
              floating: true,
              style: {
                fontSize:  '30px',
                color:  '#263238'
              },
            },
            grid: {
              show: false
            },
            xaxis: {
              labels: {
                show: false
              },
              axisBorder: {
                show: false,
              },
              axisTicks: {
                show: false,
              },
              categories: ['Jan', 'Feb', 'Mar', 'Apr', 'May', 'Jun', 'Jul', 'Aug', 'Sep'],
            },
            yaxis: {
              labels: {
                show: false
              },
              axisBorder: {
                show: false,
              },
              axisTicks: {
                show: false,
              },
              crosshairs: {
                show: false,
              }
            },
          },
          series: [{
            name: "",
            data: [10, 41, 35, 51, 49, 62, 69, 91, 148]
          }],
          userChartOptions:{},
          userSeries:[],
          getAwardChartOptions:{},
          getAwardSeries:[],
        }
      },
      mounted() {
        this.taskTableData = [
          {id:1,name: '任务名称', clickCount: '322', completeCount: '333898'},
          {id:1,name: '任务名称', clickCount: '322', completeCount: '333898'},
          {id:1,name: '任务名称', clickCount: '322', completeCount: '333898'},
          {id:1,name: '任务名称', clickCount: '322', completeCount: '333898'},
          {id:1,name: '任务名称', clickCount: '322', completeCount: '333898'},
          {id:1,name: '任务名称', clickCount: '322', completeCount: '333898'},
          {id:1,name: '任务名称', clickCount: '322', completeCount: '333898'},
          {id:1,name: '任务名称', clickCount: '322', completeCount: '333898'}
        ];
      },
      created(){
        this.getTopArea();
      },
      methods:{
        // 初始化顶部统计图
        getTopArea(){
          this.getAddUserArea();
          this.getAwardArea();
        },
        //获取新增用户量
        getAddUserArea(){
          this.userChartOptions = this.chartOptions;
          this.userChartOptions.title.text = '新增用户量';
          this.userChartOptions.xaxis.categories = ['1', '2', '3', '4', '5'] ;
          let seriesItem = {
            name:'用户',
            data:[12,43,54,67,13]
          };
          this.userSeries.push(seriesItem);
        },
        //用户获得金币总量
        getAwardArea(){
          this.getAwardChartOptions = this.chartOptions;
          this.getAwardChartOptions.title.text = '用户获得金币总量';
          this.getAwardChartOptions.xaxis.categories = ['1', '2', '3', '4', '5'] ;
          let seriesItem = {
            name:'金币',
            data:[34,13,76,25,63]
          };
          this.getAwardSeries.push(seriesItem);
        },




      }
    }
</script>

<style lang="css" scoped>
  .homeCon{
    width: 1136px;
    margin: auto;
    margin-top: 88px;
  }
  .topSelect{
    width: 128px;
    float: none;
  }
  .statisticsCon{
    /*height: 184px;*/
    height: 238px;
    margin-top: 8px;
  }
  .statisticsItem{
    width: 368px;
    /*height: 184px;*/
    height: 238px;
    float: left;
    background: #fff;
    border-radius: 4px;
    margin-right: 16px;
  }
  .noMargin{
    margin: 0;
  }
  .countTitle{
    width: 248px;
    height: 24px;
    line-height: 24px;
    opacity: .45;
    color: #000;
    margin-top: 16px;
    margin-left: 24px;
  }
  .count{
    width: 320px;
    height: 40px;
    margin-top: 8px;
    margin-left: 24px;
    font-size: 30px;
  }
  .proccessCon{
    width: 320px;
    height: 8px;
    margin-top: 24px;
    margin-left: 24px;
    background: #F5F5F5;
  }
  .nowProccess{
    width: 12%;
    height: 8px;
    background: #1890FF;
  }
  .trend{
    width: 328px;
    height: 31px;
    margin-top: 16px;
    margin-left: 24px;
    border-top: 1px solid #E8E8E8;
    padding-top: 7px;
    line-height: 24px;
  }
  .trendText{
    color: rgba(0, 0, 0, .65);
  }
  .trendCount{
    margin-left: 8px;
    color: rgba(0, 0, 0, .85);
  }
  .bestTask{
    width: 100%;
    min-height: 400px;
    background: #fff;
    border-radius: 4px;
    margin-top: 24px;
    padding-bottom: 24px;
  }
  .taskTitleCon{
    height: 56px;
    border-bottom: 1px solid #E8E8E8;
  }
  .taskTitle{
    width: 275px;
    float: left;
    font-size: 18px;
    line-height: 56px;
  }
  .taskTitle .title{
    color: rgba(0, 0, 0, .85);
    margin-left: 24px;
    font-weight:600;
  }
  .taskTitle .tips{
    margin-left: 8px;
    color: rgba(245, 34, 45, .85);
  }
  .taskSearchCon{
    float: right;
    width: 128px;
    margin-top: 12px;
    margin-right: 24px;
  }
  .homeTableCon{
    width: 1055px;
    border: 1px solid #E8E8E8;
    border-radius: 4px;
    margin:auto;
    margin-top: 24px;
    padding: 0 16px;
  }
  .getAllData{
    width: 160px;
    height: 32px;
    margin: auto;
    margin-top: 24px;
    color: rgba(0, 0, 0, 0.65);
    line-height: 32px;
    border: 1px solid #D9D9D9;
    border-radius: 4px;
    text-align: center;
  }




</style>



